/* frontend/src/app/globals.css */
:root {
  --foreground-rgb: 0, 0, 0;
  --background-start-rgb: 240, 242, 245; /* Um cinza claro para o fundo geral */
  --background-end-rgb: 255, 255, 255;

  /* Paleta Semântica - Modo Claro */
  --color-primary: #3B82F6;          /* Azul Principal (ex: botões primários, links ativos) */
  --color-primary-focus: #2563EB;   /* Azul mais escuro para hover/focus */
  --color-secondary: #10B981;        /* Verde Secundário (ex: ações complementares) */
  --color-accent: #F59E0B;           /* Laranja/Amarelo para Destaque (ex: notificações, badges) */

  --color-text: #1F2937;             /* Cinza escuro para texto principal */
  --color-text-muted: #6B7280;       /* Cinza médio para texto secundário, placeholders */

  --color-bg: #FFFFFF;               /* Branco para fundo principal de seções/páginas */
  --color-bg-alt: #F3F4F6;         /* Cinza bem claro para cards, sidebars, fundos alternativos */
  --color-border: #D1D5DB;           /* Cinza claro para bordas, divisórias */

  --color-success: #22C55E;          /* Verde para mensagens de sucesso */
  --color-error: #EF4444;            /* Vermelho para mensagens de erro */
  --color-warning: #F97316;          /* Laranja para mensagens de aviso */
  --color-info: #3B82F6;             /* Azul para mensagens informativas (pode ser o primário) */

  /* Manter suas variáveis RGB se forem usadas em gradientes complexos ou outros locais específicos */
}

[data-theme="dark"] {
  --foreground-rgb: 255, 255, 255;
  --background-start-rgb: 17, 24, 39;  /* Fundo escuro */
  --background-end-rgb: 0, 0, 0;

  /* Paleta Semântica - Modo Escuro */
  --color-primary: #60A5FA;          /* Azul mais claro */
  --color-primary-focus: #3B82F6;
  --color-secondary: #34D399;        /* Verde mais claro */
  --color-accent: #FCD34D;           /* Amarelo/Laranja mais claro */

  --color-text: #F3F4F6;             /* Cinza bem claro (quase branco) para texto */
  --color-text-muted: #9CA3AF;       /* Cinza mais claro para texto secundário */

  --color-bg: #111827;               /* Cinza bem escuro para fundo principal */
  --color-bg-alt: #1F2937;         /* Cinza escuro para cards, sidebars */
  --color-border: #374151;           /* Cinza médio-escuro para bordas */

  --color-success: #4ADE80;
  --color-error: #F87171;
  --color-warning: #FB923C;
  --color-info: #60A5FA;
}

/* Aplicar cores base ao corpo do documento */
body {
  color: var(--color-text);
  background-color: var(--color-bg); /* Ou um gradiente usando --background-start-rgb e --background-end-rgb se preferir */
}